.cards h2 {
  padding: 20px;
  width: 300px;
}

.full-card {
  position: relative;
  box-shadow: 0px 1px 5px 2px rgba(0, 0, 0, 30%);
  margin: 20px;
  height: 480px;
  width: 430px;
  border-radius: 5px;
}

.card-info {
  display: flex;
  justify-content: center;
}

.card-info img {
  margin: 20px;
  height: 40px;
}

.stats {
  margin: 20px;
}

.stats span {
  font-weight: bold;
  font-size: 15px;
}

.image-2 h2 {
  margin-left: 260px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-self: center;
  margin-bottom: 20px;
  font-size: 15px;
}

.image-2::after {
  content: '';
  display: block;
  height: 240px;
  width: 170px;
  margin-left: 260px;
  border-top-right-radius: 5px;
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.3;
  z-index: 0;
}

.image-2 {
  display: flex;
  position: relative;
}

.image-2 > * {
  position: relative;
  color: white;
  z-index: 10;
}

