.view-details-btn {
  position: absolute;
  bottom: 0;
  display: none;
  color: rgb(0, 119, 255);
}

h2 span {
  font-size: 35px;
  margin: 10px;
}

@media all and (min-width: 768px) {
  .full-card:hover {
    box-shadow: 0px 10px 10px 5px rgba(0, 0, 0, 30%);
    transition: 500ms;
    cursor: pointer;
  }

  .full-card:hover > .view-details-btn {
    transition: 500ms;
    display: block;
  }

  .view-details-btn a:hover {
    color: rgb(65, 68, 255);
    transition: 500ms;
  }
}

